import { useState, useEffect} from "react";
import { getDataFromStruct, addDataToStruct, delDataFromStruct } from "../data/handle.js";
import {commitFile} from "../gh/gh.js"
import { decryptData} from "../enc/crypt.js"


const FormScreen = props => {
    const [food, setFood] = useState(0);
    const [sport, setSport] = useState(0);
    const [alcool, setAlcool] = useState(0);
    const [tabac, setTabac] = useState(0);
    const [sleep, setSleep] = useState(0);
    const [mood, setMood] = useState(0);
    const [weight, setWeight] = useState(0);


    useEffect(() => {
        getDataFromStruct(props.db.d2, props.date, props.password)
            .then(function(data){
                setFood(data.f);
                setSport(data.s);
                setAlcool(data.a);
                setTabac(data.t);
                setSleep(data.sl);
                setMood(data.m);
                setWeight(data.w);
            })
      }, [props.db.d2, props.date, props.password]);

     async function handleCommitClick () {
        const repository = "sandrolex/sandrolex.github.io";
        const branch = "main";
        const filePath = "db.json";
        const content = JSON.stringify(props.db);
        const pat = decryptData(props.db.d1, props.password);
        if (!await commitFile(repository, branch, filePath, content, pat)) {
            props.msgHandler("Error: could not commit file", true);
        } else {
            props.msgHandler("Commit SUCCESS", false);
        }
    }

    const handleAddClick = () => {
        const data = {
            food: parseInt(food), 
            sport: parseInt(sport), 
            alcool: parseInt(alcool), 
            tabac: parseInt(tabac), 
            sleep: parseInt(sleep),
            mood: parseInt(mood),
            weight: parseInt(weight),
            date: props.date
        }
        props.db.d2 = addDataToStruct(props.db.d2, data, props.password);
        props.msgHandler("Add SUCCESS", false);
    }
    const handleDelClick = () => {
        props.db.d2 = delDataFromStruct(props.db.d2, props.date, props.password);
        props.msgHandler("Del SUCCESS", false);
    }

    const handleWeight = (value) => {
        const w = parseInt(value);
        if (isNaN(w)) { setWeight(0) }
        else {setWeight(w)}
    }


    return (
        <div>
            <p>
                <label htmlFor="date" className="form-label">{props.date.toLocaleDateString('en-GB')}</label>
            </p> 
            <div className="form-div">
                <label htmlFor="food" className="form-label">Food: </label>
                <input type="radio" value="1" name="food" checked={food === 1} onChange={(e) => setFood(parseInt(e.target.value))}/> 1&nbsp;&nbsp;
                <input type="radio" value="2" name="food" checked={food === 2} onChange={(e) => setFood(parseInt(e.target.value))}/> 2&nbsp;&nbsp;
                <input type="radio" value="3" name="food" checked={food === 3} onChange={(e) => setFood(parseInt(e.target.value))}/> 3&nbsp;&nbsp;
            </div>
            <div className="form-div">
            <label htmlFor="sport" className="form-label">Sport: </label>
                <input type="radio" value="1" name="sport" checked={sport === 1} onChange={(e) => setSport(parseInt(e.target.value))}/> 1&nbsp;&nbsp;
                <input type="radio" value="2" name="sport" checked={sport === 2} onChange={(e) => setSport(parseInt(e.target.value))}/> 2&nbsp;&nbsp;
                <input type="radio" value="3" name="sport" checked={sport === 3} onChange={(e) => setSport(parseInt(e.target.value))}/> 3&nbsp;&nbsp;
            </div>
            <div className="form-div">
            <label htmlFor="alcool" className="form-label">Alcool: </label>
                <input type="radio" value="1" name="alcool" checked={alcool === 1} onChange={(e) => setAlcool(parseInt(e.target.value))}/> 1&nbsp;&nbsp;
                <input type="radio" value="2" name="alcool" checked={alcool === 2} onChange={(e) => setAlcool(parseInt(e.target.value))}/> 2&nbsp;&nbsp;
                <input type="radio" value="3" name="alcool" checked={alcool === 3} onChange={(e) => setAlcool(parseInt(e.target.value))}/> 3&nbsp;&nbsp;
            </div>
            <div className="form-div">
            <label htmlFor="tabac" className="form-label">Tabac: </label>
                <input type="radio" value="1" name="tabac" checked={tabac === 1} onChange={(e) => setTabac(parseInt(e.target.value))}/> 1&nbsp;&nbsp;
                <input type="radio" value="2" name="tabac" checked={tabac === 2} onChange={(e) => setTabac(parseInt(e.target.value))}/> 2&nbsp;&nbsp;
            </div>
            <div className="form-div">
            <label htmlFor="sleep" className="form-label">Sleep: </label>
                <input type="radio" value="1" name="sleep" checked={sleep === 1} onChange={(e) => setSleep(parseInt(e.target.value))}/> 1&nbsp;&nbsp;
                <input type="radio" value="2" name="sleep" checked={sleep === 2} onChange={(e) => setSleep(parseInt(e.target.value))}/> 2&nbsp;&nbsp;
                <input type="radio" value="3" name="sleep" checked={sleep === 3} onChange={(e) => setSleep(parseInt(e.target.value))}/> 3&nbsp;&nbsp;
            </div>
            <div className="form-div">
            <label htmlFor="mood" className="form-label">Mood: </label>
                <input type="radio" className="form-input" value="1" name="mood" checked={mood === 1} onChange={(e) => setMood(parseInt(e.target.value))}/> 1&nbsp;&nbsp;
                <input type="radio" className="form-input" value="2" name="mood" checked={mood === 2} onChange={(e) => setMood(parseInt(e.target.value))}/> 2&nbsp;&nbsp;
                <input type="radio" className="form-input" value="3" name="mood" checked={mood === 3} onChange={(e) => setMood(parseInt(e.target.value))}/> 3&nbsp;&nbsp;
            </div>
            <div className="form-div">
            <label htmlFor="weight" className="form-label">Weight: </label>
                <input className="form-input" type="text" name="weight" size="3" value={weight.toString()} onChange={(e) => handleWeight(e.target.value)}/>
            </div>
            <div className="form-div">
                <button onClick={handleAddClick}>Add</button>
                &nbsp;
                <button onClick={handleDelClick}>Del</button>
                &nbsp;
                <button onClick={handleCommitClick}>Commit</button>
                <br/>
                <br/>
            </div>

        </div>
    );
}

export default FormScreen;
