const Message = props => {
    if (props.message === "") {
        return (<></>);
    }
    let msgClass = (props.error) ? "error-message" : "ok-message";
    let textClass = (props.error) ? "error-text" : "ok-text";
    
    return (
        <div className={msgClass}>
            <span className={textClass}>{props.message}</span>
        </div>
    );
}

export default Message;