import { useState, useEffect } from "react";
import { decryptData} from "./enc/crypt.js"

import Login from "./Login/Login"
import EditScreen from "./Screens/EditScreen"

function App() {
  const [password, setPassword] = useState("");
  const [db, setDb] = useState("");
  const [loggedIn, setLoggedIn] = useState("false");

  const loginCallback = (data) => {
    setPassword(data);
    const verif = decryptData(db.d3, data);
    if (verif === "true") {
      setLoggedIn("true");
    } else {
      console.log("failed")
    }
  }

  useEffect(() => {
    const host = window.location.host;
    let protocol = "http://";
    if (host.includes("github") ) {
      protocol = "https://";
    }
    const dbUrl = protocol + host + '/db.json';
    fetch(dbUrl).then(
		  function (res) {
			  return res.json();
		  },
    ).then(function (data) {
      // store Data in State db Variable
      setDb(data);
    }).catch(
      function (err) {
        console.log(err, ' error');
      },
    );
  }, []);
    
  if (loggedIn === "false") {
    return (
      <Login loginCallback={loginCallback} d3={db.d3}/>
    )
  } else {
    return (
    <div className="container">
      <EditScreen 
          password={password}
          db={db}
        />
    </div>
    );
  }
}

export default App;


