import { decryptData, encryptData } from "../enc/crypt.js"

const month = ["January","February","March","April","May","June","July","August","September","October","November","December"];

export const getDataFromStruct = async (d2, date, password) => {
    const cleartxt = decryptData(d2, password);
    const clearjson = JSON.parse(cleartxt);
    const YY = date.getFullYear();
    const MM = month[date.getMonth()];
    const DD = date.getDate();
    const root = {
        f: 0,
        s: 0,
        a: 0,
        t: 0,
        sl: 0,
        m: 0,
        w: 0,
    }
    if (!(YY in clearjson)) {
        return root;
    }
    if (! (MM in clearjson[YY])) {
        return root;
    }
    if (! (DD in clearjson[YY][MM])) {
        return root;
    }
    return clearjson[YY][MM][DD];
}

export const delDataFromStruct = (d2, date, password) => {
    const cleartxt = decryptData(d2, password);
    let clear = JSON.parse(cleartxt);
    const YY = date.getFullYear();
    const MM = month[date.getMonth()];
    const DD = date.getDate();
    if (!(YY in clear)) {
        return d2;
    }
    if (! (MM in clear[YY])) {
        return d2;
    }
    delete clear[YY][MM][DD];
    return encryptData(JSON.stringify(clear), password);
}

export const addDataToStruct = (d2, data, password) => {
    const cleartxt = decryptData(d2, password);
    const YY = data.date.getFullYear();
    const MM = month[data.date.getMonth()];
    const DD = data.date.getDate();
    const root = {
        f: data.food,
        s: data.sport,
        a: data.alcool,
        t: data.tabac,
        sl: data.sleep,
        m: data.mood,
        w: data.weight,
    }

    let clear = JSON.parse(cleartxt)

    if (!(YY in clear)) {
        clear[YY] = {};
    }
    if (! (MM in clear[YY])) {
        clear[YY][MM] = {};
    }
    clear[YY][MM][DD] = root;
    return encryptData(JSON.stringify(clear), password);
}